import jsPDF from "jspdf";
import moment from "moment";
import converter from "number-to-words";
import { months } from "../../constant/constant_value";
import { slashDateFormat } from "../../optimized/Utils/Functions/dateUtility";
const toPascalCase = (str) =>
  (str.match(/[a-zA-Z0-9]+/g) || [])
    .map((w) => `${w.charAt(0).toUpperCase()}${w.slice(1)}`)
    .join(" ");

const StudentLeavingnCertificate3 = (institute, studentInfo) => {
  var doc = new jsPDF({
    format: "a4",
    putOnlyUsedFonts: true,
  });

  var width = doc.internal.pageSize.getWidth();
  var firstPageHeight = 0;
  if (studentInfo?.is_letterhead) {
    firstPageHeight = 45;
    doc.setFont("WorkSans");
    doc.setTextColor(18, 18, 18);
    doc.setFontSize(10);
  } else {
    if (institute.instituteImage) {
      doc.addImage(institute.instituteImage, "JPEG", 9, 8, 25, 25);
    }
    if (institute.affiliatedImage) {
      doc.addImage(institute.affiliatedImage, "JPEG", width - 34, 7.5, 25, 25);
    }
    doc.setLineWidth(8);
    doc.setDrawColor(255, 255, 255);
    doc.circle(21.4, 20, 16);
    doc.setLineWidth(0);

    doc.setLineWidth(8);
    doc.setDrawColor(255, 255, 255);
    doc.circle(width - 21.4, 20, 16);
    doc.setLineWidth(0);

    firstPageHeight = institute?.insAffiliated ? 17 : 15;

    const instituteName = doc.splitTextToSize(institute?.insName ?? "", 146);
    doc.setFont("700");
    doc.setTextColor(18, 18, 18);
    doc.setFontSize(16);
    doc.text(107, firstPageHeight, instituteName, "center");
    if (instituteName?.length) {
      for (let i = 0; i < instituteName?.length; i++) {
        firstPageHeight += 6;
      }
    }
    if (institute?.insAffiliated) {
      doc.setFontSize(10);
      doc.text(107, 11, institute?.insAffiliated, "center");
    }

    doc.setFont("WorkSans");
    doc.setTextColor(18, 18, 18);
    doc.setFontSize(10);

    if (institute.ediatbel1) {
      let editableFirstEdit = doc.splitTextToSize(
        institute.ediatbel1,
        width - 70
      );
      doc.text(width / 2, firstPageHeight, editableFirstEdit, "center");
      if (editableFirstEdit?.length) {
        for (let i = 0; i < editableFirstEdit?.length; i++) {
          firstPageHeight += 5;
        }
      }
    }

    doc.setFont("Arial", "normal", "400");
    doc.setTextColor(18, 18, 18);
    doc.setFontSize(10);
    doc.text(width / 2, firstPageHeight, institute?.insAddress ?? "", "center");

    doc.setFont("WorkSans");
    doc.setTextColor(18, 18, 18);
    doc.setFontSize(10);

    firstPageHeight += 15;
  }

  doc.setTextColor(0, 0, 0);
  doc.setFontSize(18);
  doc.text(
    width / 2,
    firstPageHeight,
    `${
      studentInfo?.is_leaving === "MTC"
        ? "Migration TC"
        : studentInfo?.is_leaving === "MLC"
        ? "Migration LC"
        : "TRANSFER/LEAVING CERTIFICATE"
    }`,
    "center"
  );
  firstPageHeight += 11;
  if (studentInfo?.isOriginal) {
  } else {
    doc.setTextColor(234, 238, 238);
    doc.setFontSize(100);
    doc.text(60, doc.internal.pageSize.height - 100, `Duplicate`, null, 50);
  }

  doc.setFontSize(13);
  doc.setTextColor("#121212");
  doc.text(9, firstPageHeight, `T.C. No. : ${studentInfo?.certificateNumber}`);

  doc.text(
    width -
      (doc.getTextWidth(`Register No. : ${studentInfo?.enrollment}`) + 10),
    firstPageHeight,
    `Register No. : ${studentInfo?.enrollment}`
  );

  // doc.text(
  //   width - 40,
  //   firstPageHeight,
  //   `Date: ${moment(studentInfo?.date)?.format("DD/MM/yyyy")}`
  // );

  firstPageHeight += 5;
  doc.setTextColor("#121212");
  doc.setFontSize(10);
  let note = doc.splitTextToSize(
    "[ No Change in any entry in this certificate shall be made except by the authority issuing it and any infringement of this requirement is liable to involve the imposition of penalty such as that of rustication. ]",
    width - 15
  );
  firstPageHeight += 2;

  doc.setTextColor("#121212");
  doc.text(9, firstPageHeight, note);
  if (note?.length > 1) {
    for (let i = 1; i < note?.length; i++) {
      firstPageHeight += 5;
    }
  } else {
    firstPageHeight += 7.7; //140
  }

  let f_lt = firstPageHeight;
  doc.setLineWidth(0.4);
  doc.setDrawColor("#121212");
  doc.line(9, firstPageHeight + 1, width - 9, firstPageHeight + 1);

  firstPageHeight += 7.7;
  doc.setFontSize(12);

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight - 1, `1.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight - 1, `Name of Student`);
  doc.setTextColor("#000000");
  doc.text(107, firstPageHeight - 1, `${studentInfo?.name}`);

  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `2.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Mother's Name`);
  doc.setTextColor("#000000");
  doc.text(107, firstPageHeight, `${studentInfo?.motherName}`);

  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `3.`);
  doc.setTextColor("#000000");

  let css = doc.splitTextToSize(
    `Caste and Sub-Caste (In case of students belonging to Backward Classes)`,
    72
  );

  doc.text(24, firstPageHeight, css);
  doc.setTextColor("#000000");
  doc.text(107, firstPageHeight, `${studentInfo?.catse}`);
  firstPageHeight += 5;

  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `4.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Nationality`);
  doc.setTextColor("#000000");
  doc.text(107, firstPageHeight, `${studentInfo?.nationality}`);
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `5.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Place of Birth`);
  doc.setTextColor("#000000");
  doc.text(107, firstPageHeight, `${studentInfo?.birthPlace}`);
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `6.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Date of Birth (In Figures)`);
  doc.setTextColor("#000000");
  doc.text(
    107,
    firstPageHeight,
    `${moment(studentInfo?.dateOfBirth)?.format("DD/MM/yyyy")}`
  );
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `7.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Date of Birth (In Words)`);
  doc.setTextColor("#000000");

  let dobInText = "";
  if (institute?.whichCase === "Upper") {
    dobInText = `${
      studentInfo?.dateOfBirth
        ? `${toPascalCase(
            converter.toWords(
              studentInfo?.dateOfBirth?.split("T")[0]?.split("-")?.[2]
            )
          )?.toUpperCase()}  ${months[
            studentInfo?.dateOfBirth?.split("T")[0]?.split("-")?.[1] - 1
          ]?.toUpperCase()} ${toPascalCase(
            converter.toWords(
              studentInfo?.dateOfBirth?.split("T")[0]?.split("-")?.[0]
            )
          )?.toUpperCase()}`
        : "N/A"
    }`;
  } else {
    dobInText = `${
      studentInfo?.dateOfBirth
        ? `${toPascalCase(
            converter.toWords(
              studentInfo?.dateOfBirth?.split("T")[0]?.split("-")?.[2]
            )
          )}  ${
            months[studentInfo?.dateOfBirth?.split("T")[0]?.split("-")?.[1] - 1]
          } ${toPascalCase(
            converter.toWords(
              studentInfo?.dateOfBirth?.split("T")[0]?.split("-")?.[0]
            )
          )}`
        : "N/A"
    }`;
  }
  doc.text(107, firstPageHeight, dobInText);
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `8.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Last School/College Attended`);
  doc.setTextColor("#000000");

  let instituteLastAttendSplit = doc.splitTextToSize(
    // `Last School/College Attended Last School/College Attended Last School/College Attended Last School/College Attended Last School/College Attended Last School/College Attended Last School/College Attended Last School/College Attended Last School/College Attended`,
    `${studentInfo?.previousSchool}`,
    90
  );
  doc.text(107, firstPageHeight, instituteLastAttendSplit);

  if (instituteLastAttendSplit?.length) {
    for (let i = 1; i < instituteLastAttendSplit?.length; i++) {
      firstPageHeight += 5;
    }
  }

  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `9.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Date of Admission`);
  doc.setTextColor("#000000");
  doc.text(
    107,
    firstPageHeight,
    `${moment(studentInfo?.instituteJoinDate)?.format("DD/MM/yyyy")}`
  );
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `10.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Progress`);
  doc.setTextColor("#000000");
  doc.text(107, firstPageHeight, `${studentInfo?.behaviour}`);
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `11.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Conduct`);
  doc.setTextColor("#000000");
  doc.text(107, firstPageHeight, `${studentInfo?.behaviour}`);
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `12.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Date of Leaving the College`);
  doc.setTextColor("#000000");
  if (studentInfo?.instituteLeavingDate) {
    doc.text(
      107,
      firstPageHeight,
      `${slashDateFormat(studentInfo?.instituteLeavingDate)}`
    );
  }

  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;

  // let courseAndYear = `Year in which studying & since when : ### ${
  //   ` ${studentInfo?.studying}` ?? ""
  // } ### Degree in ### ${
  //   ` ${studentInfo?.degree}` ?? ""
  // } ### since when : ${` ${moment(studentInfo?.since)?.format("MMMM, yyyy")}`}`;
  // let courseAndYear = `Year in which studying & since when : ### ${` ${moment(
  //   studentInfo?.since
  // )?.format("MMMM, yyyy")}`}`;

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `13.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Year in which studying & since when`);
  let rft_dft = doc.splitTextToSize(studentInfo?.studying, 90);
  doc.text(107, firstPageHeight, rft_dft);
  if (rft_dft?.length) {
    for (let i = 1; i < rft_dft?.length; i++) {
      firstPageHeight += 5;
    }
  }
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;
  // let showingContentSplit = doc.splitTextToSize(courseAndYear, width - 20);
  // for (let content of showingContentSplit) {
  //   let c_split = content.split("###");
  //   for (let i = 0; i < c_split?.length; i++) {
  //     let s_width = 24;
  //     for (let j = 0; j < i; j++) {
  //       s_width += doc.getTextWidth(c_split[j]) - 1;
  //     }
  //     if (i % 2 === 0) {
  //       doc.setTextColor("#000000");
  //       doc.text(s_width, firstPageHeight, c_split[i]);
  //     } else {
  //       doc.setTextColor("#000000");
  //       doc.text(s_width, firstPageHeight, c_split[i]);
  //     }
  //   }
  //   firstPageHeight += 7.7;
  // }

  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `14.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Reason of leaving this college`);
  let rft = doc.splitTextToSize(studentInfo?.leavingReason, 90);
  doc.text(107, firstPageHeight, rft);
  if (rft?.length) {
    for (let i = 1; i < rft?.length; i++) {
      firstPageHeight += 5;
    }
  }
  doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  firstPageHeight += 8.7;
  doc.setTextColor("#000000");
  doc.text(11, firstPageHeight, `15.`);
  doc.setTextColor("#000000");
  doc.text(24, firstPageHeight, `Remarks :`);
  doc.setTextColor("#000000");
  let reason = doc.splitTextToSize(studentInfo?.remark, 90);
  doc.text(107, firstPageHeight, reason);
  if (reason?.length) {
    for (let i = 1; i < reason?.length; i++) {
      firstPageHeight += 5;
    }
  }

  firstPageHeight += 3;
  // doc.line(9, firstPageHeight + 3, width - 9, firstPageHeight + 3);

  doc.setLineWidth(0.4);
  doc.setDrawColor("#121212");
  doc.line(9, f_lt + 1, 9, firstPageHeight);

  doc.line(22, f_lt + 1, 22, firstPageHeight);

  doc.line(105, f_lt + 1, 105, firstPageHeight);
  doc.line(width - 9, f_lt + 1, width - 9, firstPageHeight);
  doc.line(9, firstPageHeight, width - 9, firstPageHeight);

  firstPageHeight += 7;
  doc.setTextColor("#000000");
  doc.setFontSize(10);
  doc.text(
    11,
    firstPageHeight,
    "Certified that above information is in accordance with the college register & this college has no objection to seek admission any where."
  );
  firstPageHeight += 10;
  doc.setTextColor("#000000");
  doc.setFontSize(13);

  doc.text(
    11,
    firstPageHeight,
    `Date of Issue: ${moment(studentInfo?.date)?.format("DD/MM/yyyy")}`
  );
  firstPageHeight += 28;

  let authority = studentInfo?.authority
    ? `${studentInfo?.authority}:`
    : "Principal";

  doc.setTextColor("#000000");
  doc.setFontSize(14);

  doc.text(width - 70, firstPageHeight, authority);

  firstPageHeight += 6;

  doc.setTextColor("#000000");
  doc.setFontSize(14);

  doc.text(30, firstPageHeight, `Prepared By`);

  doc.setFontSize(14);

  doc.setTextColor("#000000");
  // let instituteName_d = doc.splitTextToSize(
  //   "hvsab gm , df sadhgds b sdm hfsd hvsab gm , df sadhgds b sdm hfsd hvsab gm , df sadhgds b sdm hfsd hvsab gm , df sadhgds b sdm hfsd",
  //   100
  // );
  const instituteName_d = doc.splitTextToSize(institute?.insName, 100);
  doc.text(150, firstPageHeight, instituteName_d, "center");
  return doc.output("blob");

  // window.open(
  //   doc.output("bloburl", {
  //     filename: `leaving.pdf`,
  //   }),
  //   "_blank"
  // );
};

export default StudentLeavingnCertificate3;

